.svgIcon {
  width: 20px;
  height: 20px;
  // fill: red;
}

html {
  margin: 0;
  padding: 0;
  font-size: 62.5% !important;
  color: @txtColor1;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  -webkit-tap-highlight-color: transparent;
  background: @bgColor;
}

p {
  margin: 0;
  word-break: break-word;
}

::-webkit-scrollbar {
  width: 2px;
  height: 3px;
  background-color: #f5f5f5;
  border-radius: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.123);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.123);
  background-color: rgb(255, 255, 255);
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.11);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.11);
  background-color: @themeColor;
}


::-webkit-scrollbar-thumb:hover {
  background-color: @themeColor;
}

.w100 {
  width: 100%;
}

.safetyWidth {
  margin: auto;
  max-width: 1200px;

  @media @mobile {
    width: 92%;
  }

  @media @desktop {
    // width: 1400px;
  }
}

