  float: right;
}

.clearfix:after {
  content: "";
  height: 0;
  line-height: 0;
  display: block;
  visibility: hidden;
  clear: both;
}

.clearfix {
  zoom: 1;
}

.text_c {
  text-align: center;
}

.text_r {
  text-align: right;
}

.text_l {
  text-align: left;
}

.text_b {
  vertical-align: text-bottom;
}

.uppercase {
  text-transform: uppercase;
}

.margin_auto {
  margin: auto;
}

.center_grid {
  display: grid;
  place-content: center;
}

.text_j {
  text-align: justify;
}

.breakWord {
  word-break: break-word;
}

.textIndent {
  text-indent: 3rem;
}

.ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}

.nowrap {
  white-space: nowrap;
}

ul,
ol,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.pointer {
  cursor: pointer;
}

.no-drop {
  cursor: no-drop !important;
}

.break {
  word-break: break-all;
}

.alertDiv {
  max-width: 70%;
  height: 50px;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 3999;
  text-align: center;
}

.alertTXT {
  display: inline-block;
  min-width: 10rem;
  padding: 1.5rem 2rem;
  margin: auto;
  text-align: center;
  color: rgb(255, 255, 255);
  background: rgba(0, 0, 0, 0.65);
  border-radius: 5px;
  box-sizing: border-box;
  word-break: break-word;
}

.th-card {
  border-radius: 30px;
  background: @bgColor;
  box-shadow: 5px 5px 7px #cccccc,
    -5px -5px 7px #ffffff;
}
