.walletModal {
    width: 25rem;
    min-height: 10rem;
    position: fixed;
    z-index: 99;
    top: 30%;
    left: 0;
    right: 0;
    margin: auto;
    padding: 3rem 3rem;
    background: #e5e5e5;
    border-radius: 2rem;
    box-shadow: 0 0 2.5rem 0rem #b9b9b9;

    @media @mobile {
        width: 68%;
    }

    ul {
        li {
            padding: 2rem;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            margin: 2rem 0;
            transition: 0.3s;
            border: 1px solid #cfcfcf;
            border-radius: 13px;
            font-size: 1.8rem;
            color: #4e4e4e;


            span {
                margin-right: 1rem;
                font-size: 0;
            }

            &:hover {
                background: #e0e0e0;
                box-shadow: inset 5px 5px 10px #cacaca,
                    inset -5px -5px 10px #f6f6f6;
                transition: 0.3s;
                border: 1px solid #e7e7e7;
                color: #3c3c3c;
            }
        }
    }

    .close {
        position: absolute;
        right: 1.2rem;
        top: 1.2rem;
        cursor: pointer;

        .svg-icon {
            width: 3rem;
            height: 3rem;
            fill: #7c7c7c;
        }
    }
}
@primary-color: #1DA57A;@desktop: ~'only screen and (min-width: 760px)';@mobile: ~'only screen and (max-width: 760px)';